.resource-content {
    &__hidden {
        @apply opacity-0 -left-full max-w-0 max-h-0 overflow-hidden;
        transition-property: opacity, left, max-width;
        transition-duration: .6s, .5s, .3s;
        transition-delay: .2s;
    }
    &__show {
        @apply opacity-100 left-0 max-w-[1080px] max-h-[10000px];
        transition-property: opacity, left, max-width;
        transition-duration: .6s, .5s, .3s;
        transition-delay: .5s;
    }
}
.resource-type-selected:hover{
    @apply text-black;
    transform: none !important;
}
.dribble-media-linked:hover figure img{
    @apply scale-105
}